"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";

export default function EnrollmentPage() {
  const [user, setUser] = useState<any>(null);
  const [selectedPlan, setSelectedPlan] = useState<string | null>(null);
  const [enrollmentStep, setEnrollmentStep] = useState(1);

  useEffect(() => {
    // Check if user is logged in
    const userData = localStorage.getItem("user");
    if (userData) {
      setUser(JSON.parse(userData));
    }
  }, []);

  const plans = [
    {
      id: "silver",
      name: "Silver",
      price: "₹9,999",
      period: "/month",
      description: "Perfect for beginners",
      features: ["3 courses per month", "Basic materials", "Community access", "Email support"]
    },
    {
      id: "gold",
      name: "Gold",
      price: "₹19,999",
      period: "/month",
      description: "Most popular choice",
      popular: true,
      features: ["ALL courses", "Live sessions", "1-on-1 mentor support", "Career guidance"]
    },
    {
      id: "platinum",
      name: "Platinum",
      price: "₹34,999",
      period: "/month",
      description: "Complete professional package",
      features: ["Everything in Gold", "Personal coach", "Job placement", "Lifetime access"]
    }
  ];

  const enrollmentSteps = [
    { step: 1, title: "Authentication", description: "Sign in or create account" },
    { step: 2, title: "Plan Selection", description: "Choose your membership plan" },
    { step: 3, title: "Payment", description: "Complete your enrollment" }
  ];

  const handlePlanSelect = (planId: string) => {
    setSelectedPlan(planId);
    setEnrollmentStep(3);
  };

  const getStepStatus = (step: number) => {
    if (step < enrollmentStep) return "completed";
    if (step === enrollmentStep) return "current";
    return "upcoming";
  };

  return (
    <div className="min-h-screen py-8 px-4">
      <div className="container mx-auto max-w-4xl">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl md:text-5xl font-bold text-black mb-4">
            Enrollment Process
          </h1>
          <p className="text-xl text-gray-600">
            Complete your enrollment in just a few simple steps
          </p>
        </div>

        {/* Progress Steps */}
        <div className="mb-12">
          <div className="flex justify-between items-center mb-4">
            {enrollmentSteps.map((item, index) => (
              <div key={item.step} className="flex items-center">
                <div className={`
                  w-10 h-10 rounded-full flex items-center justify-center text-sm font-bold
                  ${getStepStatus(item.step) === 'completed' ? 'bg-green-500 text-white' : ''}
                  ${getStepStatus(item.step) === 'current' ? 'bg-black text-white' : ''}
                  ${getStepStatus(item.step) === 'upcoming' ? 'bg-gray-200 text-gray-500' : ''}
                `}>
                  {getStepStatus(item.step) === 'completed' ? '✓' : item.step}
                </div>
                {index < enrollmentSteps.length - 1 && (
                  <div className={`
                    w-16 md:w-32 h-1 mx-2
                    ${getStepStatus(item.step + 1) !== 'upcoming' ? 'bg-green-500' : 'bg-gray-200'}
                  `}></div>
                )}
              </div>
            ))}
          </div>
          
          <div className="flex justify-between text-center">
            {enrollmentSteps.map((item) => (
              <div key={item.step} className="flex-1">
                <h3 className="font-semibold text-sm">{item.title}</h3>
                <p className="text-xs text-gray-500">{item.description}</p>
              </div>
            ))}
          </div>
        </div>

        {/* Step Content */}
        {enrollmentStep === 1 && (
          <Card>
            <CardHeader className="text-center">
              <CardTitle>Step 1: Authentication Required</CardTitle>
              <CardDescription>
                Please sign in to your account or create a new one to continue with enrollment
              </CardDescription>
            </CardHeader>
            <CardContent className="text-center space-y-4">
              {user ? (
                <div>
                  <div className="bg-green-50 border border-green-200 text-green-700 px-4 py-3 rounded-md mb-4">
                    Welcome back, {user.name}! You're signed in and ready to proceed.
                  </div>
                  <Button onClick={() => setEnrollmentStep(2)} size="lg">
                    Continue to Plan Selection
                  </Button>
                </div>
              ) : (
                <div className="space-y-4">
                  <p className="text-gray-600">
                    To enroll in our courses, you need to have an account with us.
                  </p>
                  <div className="flex flex-col sm:flex-row gap-4 justify-center">
                    <Link href="/auth/login">
                      <Button size="lg" className="w-full sm:w-auto">
                        Sign In
                      </Button>
                    </Link>
                    <Link href="/auth/signup">
                      <Button variant="outline" size="lg" className="w-full sm:w-auto">
                        Create Account
                      </Button>
                    </Link>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        )}

        {enrollmentStep === 2 && (
          <div>
            <div className="text-center mb-8">
              <h2 className="text-2xl font-bold mb-2">Step 2: Choose Your Plan</h2>
              <p className="text-gray-600">Select the membership plan that best fits your learning goals</p>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {plans.map((plan) => (
                <Card 
                  key={plan.id} 
                  className={`relative cursor-pointer transition-all hover:shadow-lg ${
                    plan.popular ? 'border-2 border-black' : ''
                  } ${selectedPlan === plan.id ? 'ring-2 ring-black' : ''}`}
                  onClick={() => handlePlanSelect(plan.id)}
                >
                  {plan.popular && (
                    <div className="absolute -top-3 left-1/2 transform -translate-x-1/2">
                      <Badge className="bg-black text-white">Most Popular</Badge>
                    </div>
                  )}
                  
                  <CardHeader className="text-center">
                    <CardTitle>{plan.name}</CardTitle>
                    <div className="mt-2">
                      <span className="text-3xl font-bold">{plan.price}</span>
                      <span className="text-gray-500">{plan.period}</span>
                    </div>
                    <CardDescription>{plan.description}</CardDescription>
                  </CardHeader>
                  
                  <CardContent>
                    <ul className="space-y-2 mb-6">
                      {plan.features.map((feature, index) => (
                        <li key={index} className="flex items-center text-sm">
                          <span className="w-2 h-2 bg-green-500 rounded-full mr-3"></span>
                          {feature}
                        </li>
                      ))}
                    </ul>
                    
                    <Button className="w-full">
                      Select {plan.name}
                    </Button>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
        )}

        {enrollmentStep === 3 && (
          <Card>
            <CardHeader className="text-center">
              <CardTitle>Step 3: Complete Payment</CardTitle>
              <CardDescription>
                You're almost done! Complete your payment to start learning.
              </CardDescription>
            </CardHeader>
            <CardContent className="text-center">
              {selectedPlan && (
                <div className="bg-gray-50 p-6 rounded-lg mb-6">
                  <h3 className="font-semibold mb-2">Selected Plan:</h3>
                  <div className="text-2xl font-bold">
                    {plans.find(p => p.id === selectedPlan)?.name} Plan
                  </div>
                  <div className="text-xl text-gray-600">
                    {plans.find(p => p.id === selectedPlan)?.price}/month
                  </div>
                </div>
              )}
              
              <Link href="/enrollment/payment">
                <Button size="lg" className="w-full sm:w-auto">
                  Proceed to Payment
                </Button>
              </Link>
            </CardContent>
          </Card>
        )}

        {/* Help Section */}
        <div className="mt-12 text-center">
          <h3 className="text-xl font-semibold mb-4">Need Help?</h3>
          <p className="text-gray-600 mb-4">
            Our support team is here to assist you with the enrollment process.
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Button variant="outline">
              Chat with Support
            </Button>
            <Button variant="outline">
              Call +918618764349
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}
